body{
  margin: 20px;
  height: 100vh;
  overflow: hidden;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

h1,ul,li,a,p{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  color: black;
}


.board{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.info{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 100px;
}



.word{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 100px;
  width: 300px;
}

.keyBoard{
  margin-bottom: 50px;
}

.letter{
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
  /* border: 1px solid black; */
  font-weight: 600;
}

.letter:not(:last-child){
  margin-right: 15px;
}

.correct{
  background-color: green;
  color: white;
}

.wrong{
  background-color: red;
  color: white;
}

.guesses{
  font-size: 16px;
}

.guesses span{
  font-size: 25px;
  font-weight: bold;
}

button{
  justify-self: flex-end;
  padding: 10px;
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: green;
  cursor: pointer;
}

button:focus{
  outline: none;
}

.result{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: 900;
  -webkit-animation: result 1s forwards;
          animation: result 1s forwards;
}

@-webkit-keyframes result{
  to{
    -webkit-transform: translate(-50%, -50%) scale(6);
            transform: translate(-50%, -50%) scale(6);    
  }
}

@keyframes result{
  to{
    -webkit-transform: translate(-50%, -50%) scale(6);
            transform: translate(-50%, -50%) scale(6);    
  }
}


.keyBoard{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 100px;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  text-align: center;
  width: 600px;
  height: 200px;
}

.alpha{
  width: 20px;
  height: 20px;
  text-align: center;
  border: 1px solid black;
  padding: 10px;
  cursor: pointer;
}

.alpha:not(:last-child){
  margin-right: 10px;
}
